<template>
  <div class="top">
    <van-cell-group>
      <van-field
        v-model="nick"
        required
        clearable
        label="nick"
        placeholder="输入可选"
        style="border: 1px solid #ccc"
      />

      <van-field
        v-model="password"
        type="password"
        label="password"
        placeholder="请输入密码"
        required
        style="border: 1px solid #ccc"
      />
      <van-field
        v-model="phone"
        type="phone"
        label="phone"
        placeholder="请输入电话"
        required
        style="border: 1px solid #ccc"
      />
    </van-cell-group>
<div class="login">

    <button class="btn" @click="login()">登录</button>
</div>
  <br>
    <router-link to="/register">没有账号？去注册</router-link>
<!--    <h1>{{count}}</h1>-->
<!--    <h1>{{count2}}</h1>-->

    <!--    <h2>{{count}}</h2>-->
<!--    <h3>{{this.$store.state.count1}}</h3>-->
<!--    <h3>{{count3}}</h3>-->
<!--    <h3>{{count4}}</h3>-->

<!--    <button @click="add">+</button>-->
<!--    <button @click="jian">-</button>-->
<!--    <h1>{{this.$store.state.info.count}}</h1>-->


  </div>
</template>

<script>
  import axios from 'axios';
  import {mapState} from 'vuex';
  //import {mapGetters} from 'vuex';
  //import {mapActions} from 'vuex';
  //import {mapGetters} from 'vuex';

  import { mapMutations } from 'vuex';

  export default {
    name: "login",
    data(){
      return{
        nick:'',
        password:'',
        phone:'',
        title:'登录'
      }

    },
    mounted() {

      this.$emit('totitle',this.title)
      this.token()
    },
    computed:{
      ...mapState(['count','count3',"count4"]),
      //...mapGetters(['count2'])
    },
    methods:{
      ...mapMutations(['changeLogin']),
      //...mapActions(['jian']),
      login(){
        var _this=this
        axios({
          method:'post',
          url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/user/login.do',
          data:{
            nick:this.nick,
            password:this.password,
            phone:this.phone
          }
        }).then(function(data){
          var str=data.data
          console.log(str)
          _this.$notify(str.msg);

          if(str.code==1){
            _this.changeLogin({ Authorization: str.data })
          _this.$router.push('/page')
            //console.log(str.data)
            _this.$store.state.count=str.data
          }
        })

      },


      add(){
        this.$store.commit('add')
      },
      jian(){
        this.$store.commit('jian')
      },
      token(){
        var _this=this
        axios({
          url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/user/checkLogin.do',
          params:{usertoken:localStorage.getItem('Authorization')}
        }).then((data)=>{
          //console.log(data.data)
          var str=data.data
          console.log(str.data)
          _this.$store.state.userid=str.data.id
          _this.$store.state.usernames=str.data.nick
          if(str.code==0){
            //alert(str.msg)
            //_this.$toast('提示文案')
            _this.$router.push('/login')
          }
        })
      }


    }
  }
</script>

<style scoped>
  .top{
    margin-top: 50px;
  }
    .btn{
      width: 200px;
      height: 40px;
      border-radius: 9px;
      background: #1c8de0;
      color: #fff;
    }
  .login{
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>



