<template>
    <div>
      <van-pull-refresh :style="zIndex=999" v-model="isLoading" @refresh="onRefresh">
        <p></p>





      <van-swipe :autoplay="3000">
        <van-swipe-item v-for="(image, index) in images" :key="index">

          <img class="img1" :src="image.banner_img_url"/>
        </van-swipe-item>
      </van-swipe>


        <div v-for="item in list" class="list" >
          <div class="goods" @click="detail(item.pid)">
          <img class="img2" :src="item.pimg" alt="" />
          <span>{{item.pname}}</span>
          <span>￥{{item.pprice}}</span>
          </div>
        </div>

      <div v-for="item in list" class="list" >
        <div class="goods" @click="detail(item.pid)">
          <img class="img2" :src="item.pimg" alt="" />
          <span>{{item.pname}}</span>
          <span>￥{{item.pprice}}</span>
        </div>
      </div>
      <div class="title">
        <span>限时秒杀</span>
        <span @click="daojishi">距离结束还剩{{time}}</span>
      </div>

      <div v-for="item in list" class="shot">

        <div class="good" @click="detail(item.pid)">
          <img class="img2" :src="item.pimg" alt="" />
          <span>{{item.pname}}</span>
          <strike style="color: #ccc">￥{{item.pprice*2}}</strike>
          <span>￥{{item.pprice}}</span>
        </div>
      </div>

      </van-pull-refresh>
    </div>


</template>

<script>


  import axios from 'axios'
    export default {
        name: "Page",
      data(){
        return{
          title:'首页',
          images:[],
          list:[],
          count: 0,
          isLoading: false,
          time:''

        }
      },
      methods:{
        detail(id){
          this.$router.push({name:'Detail',query:{id:id}})

        },

          daojishi(){

                var timer = setInterval(foo,1000)
                var that = this
                function foo(oDate1){
                  var oDate1 = new Date("2019-05-15 9:16:35");
                  var oDate2 = new Date();
                  var ss = (oDate1-oDate2)/1000;
                  var day=checkTime(Math.floor(ss/3600/24%30))
                  var hour = checkTime(Math.floor(ss/3600%24));
                  var minute = checkTime(Math.floor(ss/60%60));
                  var second = checkTime(Math.floor(ss%60));
                  that.time=day+':'+hour+":"+minute+":"+second;

                  if(ss<=0){
                    this.time = "秒杀结束";
                    clearInterval(timer);
                  }

                }
                function checkTime(i){
                  if(i<10)
                  {
                    i = "0" + i;
                  }
                  return i;
            }
          },





        onRefresh() {
          setTimeout(() => {
            this.$toast('刷新成功');
            this.isLoading = false;
            this.count++;
          }, 500);
        }
      } ,




      mounted(){
        this.$emit('toTitle',this.title)

        this.daojishi()
        var _this=this


//			轮播
        axios({
          url:'http://jx.xuzhixiang.top/ap/api/bannerlist.php',
          params:{uid:1932}
        }).then((data)=>{
          _this.images=data.data.data
        })

        axios({
          url:'http://jx.xuzhixiang.top/ap/api/productlist.php',
          params:{uid:1932}
        }).then((data)=>{
          //console.log(data.data.data)
          _this.list=data.data.data
        })

      }
    }
</script>

<style scoped>

  .img1{
    width: 100%;
    height: 150px;
  }
  .img2{
    width: 60px;
    height: 50px;
    border: 1px solid #000;
  }
  .list .goods{
    float: left;
    width: 70px;
    height: 100px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 12px;
  }
.list{
  width: 100%;

}
.title{
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: orangered;

}
  .shot{
    width: 100%;


  }
  .shot .good{
    float: left;
    width: 70px;
    height: 100px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 12px;


  }
</style>



