<template>
    <div>
      <Begin/>
      <h1>index2</h1>
<!--      <li v-for="item in list">{{item}}</li>-->

    </div>
</template>

<script>
  import Begin from '@/components/Begin'
  import axios from 'axios'
    export default {
        name: "index2",
        data(){
          return{
            list:[]
          }
        },
      methods:{


      },
      components:{
       Begin

      },
      mounted() {
        var _this=this;
        axios({
          method:'get',
          url:'http://jx.xuzhixiang.top/ap/api/productlist.php',
          params:{uid:1932}
        }).then(function (data) {
          console.log(data.data.data)
          _this.list=data.data.data
        })
      }
    }
</script>

<style scoped>
h1{
  color: #1c8de0;
  font-size: 45px;
}
</style>



