<template>
  <div>
    <Free :name="title"/>
    <div class="container">
    </div>
    <van-card
      v-for="(item,k) in list"
      :price="item.pprice"
      :desc="item.pdesc"
      :title="item.pname"
      :thumb="item.pimg"
      :num="item.pnum"
    >

      <div slot="footer">
        {{item.checked}}
        总价：￥{{item.pnum*item.pprice}}
        <van-button size="mini" @click="del(item.pid)">删除</van-button>

        <input type="checkBox" v-bind:checked="item.isSelect" @click="item.isSelect=!item.isSelect"></input>
      </div>
    </van-card>
    <van-submit-bar
      :price="getTotal.totalPrice*100"
      button-text="提交订单"
      @submit="onSubmit"
    >
      全选:<input type="checkBox" @click="hcheck(isSelectAll)" v-bind:checked="isSelectAll" ></input>
    </van-submit-bar>

  </div>
</template>

<script>
  import {mapState} from 'vuex';

  import axios from 'axios';
  import Free from '@/components/Free'
  export default {
    name: "Cart",
    components:{
      Free
    },
    data(){
      return{
        title:'购物车',
        item:'',
        cartData:{},
        list:[],
        radio: '1',
        tprice:1,




      }
    },
    computed:{
      ...mapState(['count']),
      isSelectAll:function(){
        //如果List中每一条数据的isSelect都为true，返回true，否则返回false;
        return this.list.every(function (val) { return val.isSelect});
      },

      getTotal:function(){
        var prolist = this.list.filter(function (val) { return val.isSelect}),
          totalPri = 0;
        for (var i = 0,len = prolist.length; i < len; i++) {
          totalPri+=prolist[i].pprice*prolist[i].pnum;
         this.$store.state.totalmoney=totalPri
        }
        return {totalNum:prolist.length,totalPrice:totalPri}
      },
    },
    mounted() {
      this.token()
      console.log(this.tprice);
      var _this=this;
      axios({
        url:'http://jx.xuzhixiang.top/ap/api/cart-list.php',
        params:{id:1932}
      }).then((data)=>{
         console.log(data.data.data)
        //_this.list=data.data.data
        var str=data.data.data

        str.map(function (item,index) {
          item.isSelect=false

        })
        //console.log(str)
        _this.list=str

      })

    },
    methods:{
      hcheck(_isSelect) {
        for (var i = 0, len = this.list.length; i < len; i++) {
          this.list[i].isSelect = !_isSelect;
        }


      },
      onSubmit(){
        this.$router.push('/account')
      },

      del(pid){

        var _this=this;
        axios({
          url:'http://jx.xuzhixiang.top/ap/api/cart-delete.php',
          params:{uid:1932,pid:pid}
        }).then((data)=>{

          //console.log(data.data.data)
          //_this.list=data.data.data
          location.reload()

        })
      },

      token(){
        var _this=this
        axios({
          url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/user/checkLogin.do',
          params:{usertoken:localStorage.getItem('Authorization')}
        }).then((data)=>{
            console.log(data.data)
          var str=data.data
          if(str.code==0){
            alert(str.msg)
            //_this.$toast('提示文案')

            _this.$router.push('/login')
          }
        })
      }


    }
  }
</script>

<style scoped>
  .container{
    margin-top: 46px;
    margin-bottom: 50px;
  }
</style>



