import axios from 'axios';

var mutations={
	add:function(state){
		state.count++
	},
	jian:function(state){
		state.count--
	},
	login(state,username){
		state.username=username
	},
  changeLogin(state,user) {
    state.Authorization = user.Authorization;
    localStorage.setItem('Authorization', user.Authorization);
  },
  token(){
    var _this=this
    axios({
      url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/user/checkLogin.do',
      params:{usertoken:localStorage.getItem('Authorization')}
    }).then((data)=>{
      console.log(data.data)
      var str=data.data
      if(str.code==0){
        alert(str.msg);
        //_this.$toast('提示文案')
       // _this.$store.state.userid=str.data.id
        _this.$router.push('/login')
      }
    })
  }




}

export default mutations;



