import Vue from 'vue'
import Router from 'vue-router'
import index2 from '@/view/index2'
import Main from '@/view/Main'
import Login from '@/view/Login'
import Register from '@/view/Register'
import Home from '@/view/Home'
import Page from '@/view/Page'
import Friends from '@/view/Friends'
import Search from '@/view/Search'
import Setting from '@/view/Setting'
import Server from '@/view/Server'
import Chat from '@/view/Chat'
import Cart from '@/view/Cart'
import Detail from '@/view/Detail'
import Voucher from '@/view/Voucher'
import Backlogin from '@/view/Backlogin'
import New from '@/view/New'
import Account from '@/view/Account'


Vue.use(Router);

export default new Router({
  routes: [{
    path:'/',
    redirect:'/login',
    name:'index2',
    component:index2

    },
    {
      path:'/main',

      name:'Main',
      component:Main,
    },
      {

        path:'/login',
        name:'Login',
        component:Login
      },{

        path:'/register',
        name:'Register',
        component:Register
      }

   ,{
      path:'/home',
      name:'Home',
      component:Home,
      children: [{
        path:'/page',
        name:'Page',
        component:Page
      },
        {
          path:'/friends',
          name:'Friends',
          component:Friends,

        },
        {
          path:'/search',
          name:'Search',
          component:Search
        },
      ]

    },
    {
      path:'/setting',
      name:'Setting',
      component:Setting
    },
    {
      path:'/server',
      name:'Server',
      component:Server
    },
    {
      path:'/chat',
      name:'Chat',
      component:Chat
    },
    {
      path:'/detail',
      name:'Detail',
      component:Detail
    },
    {
      path:'/cart',
      name:'Cart',
      component:Cart
    },
    {
      path:'/voucher',
      name:'Voucher',
      component:Voucher
    },
    {
      path:'/backlogin',
      name:'Backlogin',
      component:Backlogin
    },
    {
      path:'/new',
      name:'New',
      component:New
    },
    {
      path:'/account',
      name:'Account',
      component:Account
    }


  ]
})
 /*Router.beforeEach((to, from, next) => {
  if (to.path === '/login') {
    next();
  } else {
    let token = localStorage.getItem('Authorization');

    if (token === 'null' || token === '') {
      next('/login');
    } else {
      next();
    }
  }
});*/




