<template>

  <div class="top">
    <van-icon @click="onClickLeft" style="color: #ccc;font-size: 33px;margin-left: 10px;margin-top: 10px" name="service-o"/>
    <van-search v-model="value" @blur="search" placeholder="请输入搜索关键词" />
    <van-icon @click="onClickRight" style="font-size: 33px;margin-right: 10px;margin-top: 10px" name="chat-o"/>
    <div class="show">
      <van-card
        v-for="ite in item"
        :price="ite.foodPrice"
        desc="描述信息"
        :title="ite.foodName"
        :thumb="ite.foodImg"
      />
    </div>
  </div>


</template>

<script>
import axios from 'axios'
  export default {
    name: 'Head',
    props:['name'],
    data () {
      return {
        msg: 'public head',
        title:'主页',
        value:'猪蹄',
        item:[]

      }

    },
    mounted(){





    },
    methods:{
      search(){
        var _this=this


//			轮播
        axios({
          url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/food/selectByMsg.do',
          params:{msg:_this.value}
        }).then((data)=>{
          console.log(data.data.data)
          _this.item=data.data.data


        })


      },
      onClickLeft(){
      this.$router.push('/server')
      },
      onClickRight(){
        this.$router.push('/chat')

      }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .top{
   width: 100%;
   display: flex;
   justify-content: space-between;
   color: #ccc;
  position: fixed;
   top: 0;
   background: #fff;

   z-index: 999;
 }
  .show{
    position: absolute;
    background: #fff;
    width: 80%;
    top: 50px;
    left: 40px;



  }
</style>



