<template>


  <div>
    <!--列表-->
    <van-card
      v-for="item in list"
      :price="item.pprice"
      :desc="item.pdesc"
      :title="item.pname"
      :thumb="item.pimg"
      @click="detail(item.pid)"
    />

  </div>
</template>

<script>
  import axios from 'axios'
  export default{
    name:'Friends',
    data(){
      return{
        title:'首页',
        images:[],
        list:[]
      }
    },
    mounted(){
      this.$emit('toTitle',this.title)
      var _this=this;


//			数据列表
      axios({
        url:'http://jx.xuzhixiang.top/ap/api/productlist.php',
        params:{uid:1932}
      }).then((data)=>{
        _this.list=data.data.data
      })
    },
    methods:{
      detail(id){
        this.$router.push({name:'Detail',query:{id:id}})

      }
    }
  }
</script>

<style scoped="">
  img{
    width: 100%;
    height: 200px;
  }
</style>



