<template>
    <div>
      <Top/>
      <div class="container">
      </div>
      <van-coupon-cell
        :coupons="coupons"
        :chosen-coupon="chosenCoupon"
        @click="showList = true"
      />

      <!-- 优惠券列表 -->
      <van-popup v-model="showList" position="bottom">
        <van-coupon-list
          :coupons="coupons"
          :chosen-coupon="chosenCoupon"
          :disabled-coupons="disabledCoupons"
          @change="onChange"
          @exchange="onExchange"
        />

      </van-popup>

    </div>
</template>

<script>

  const coupon = {
    available: 1,
    condition: '无使用门槛\n最多优惠12元',
    reason: '',
    value: 150,
    name: '优惠券名称',
    startAt: 1489104000,
    endAt: 1594592000,
    valueDesc: '1.5',
    unitDesc: '元'
  };
  import axios from 'axios'
  import Top from '@/components/Top'
    export default {
    name: "Voucher",
     components:{
          Top
      },
      data() {
        return {
          chosenCoupon: -1,
          coupons: [coupon,coupon,coupon],
          disabledCoupons: [coupon],
          showList:'',
          list:[]
        }
      },
      methods: {
        onChange(index) {
          this.showList = false;
          this.chosenCoupon = index;
        },
        onExchange(code) {
          this.coupons.push(coupon);
        }
      },
      mounted() {
        var _this=this;
     //console.log(this.$store.state.userid)
        axios({
          method:'get',
          url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/userVoucher/selectByUserId.do',
          params:{
            userId:1

          }
        }).then((data)=>{
          console.log(data.data.data)
           _this.list=data.data.data
        })
      }

    }
</script>

<style scoped>
  .container{
    margin-top: 46px;
    margin-bottom: 50px;
  }
</style>



