<template>
    <div>
      <Head :name="title"/>
      <div class="container">
<!--        <p>{{this.$store.state.count}}</p>-->
        <router-view @toTitle="tap"></router-view>
    </div>
      <van-tabbar v-model="active" fixed>

        <van-tabbar-item icon="home-o" to="/page">首页</van-tabbar-item>
        <van-tabbar-item icon="search" to="/search">全部菜品</van-tabbar-item>
        <van-tabbar-item icon="orders-o
"  to="/friends">常用清单</van-tabbar-item>
        <van-tabbar-item icon="cart-o"to="/cart">购物车</van-tabbar-item>
        <van-tabbar-item icon="setting-o"  to="/setting">设置</van-tabbar-item>
      </van-tabbar>
      <div></div>
    </div>

</template>

<script>
  import {mapState} from 'vuex';

  import Head from '@/components/Head'
    export default {
        name: "Home",
      components:{
          Head

      },
      data(){
        return{
          title:'首页',
          active:0

        }
      },
      methods:{
        tap(msg){
          this.title=msg
        }
      },
      computed:{
        ...mapState(['count','count3',"count4"]),

      }
    }

</script>

<style scoped>
  .container{
    margin-top: 46px;
    margin-bottom: 50px;
  }
</style>



