<template>
  <div>
    <Top :name="title"/>
    <div class="container">

    </div>
    <div class="box">
      <span>地址 :</span>
      <input v-model="address"></input>

    </div>
    <div class="box">
      <span>电话  :</span>
      <input v-model="phone"></input>
    </div>
    <div class="box">
      <span>姓名  :</span>
      <input v-model="name"></input>

    </div>
    <div class="box">
      <span>金额  :</span>
      <span>{{this.$store.state.totalmoney}}</span>
    </div>
    <div class="box">
      <span>订单  :</span>
      <span>0000{{this.$store.state.userid}}</span>
    </div>
    <button @click="tag">确认修改</button>
    <button @click="del">删除地址</button>
<div class="bottom">
  <button>立即购买</button>
</div>
  </div>

</template>

<script>
  import Top from '@/components/Top'
import axios from 'axios'
  export default {
    name: "Account",
    data(){
      return{
        title:'订单信息',
        address:'',
        phone:'',
        order:'',
        money:'',
        name:'',
        list:[],
        id:''
      }

    },
    components:{
      Top
    },
    mounted(){
      var _this=this
      axios({
        method:'get',
        url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/userAddress/selectByUserId.do',
        params:{
          id:_this.$store.state.userid
        }
      }).then((data)=>{
        console.log(data.data.data)
        _this.list=data.data.data
        console.log(_this.list[0].name)
        _this.name=_this.list[0].name
        _this.address=_this.list[0].address
        _this.phone=_this.list[0].tel
        _this.id=_this.list[0].id
      })


    },
    methods:{
      tag(){
        var _this=this
        axios({
          method:'post',
          url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/userAddress/save.do',
          data:{
            id:_this.$store.state.userid,
            address: this.address,
            name: this.name,
            tel: this.phone
          }
        }).then(function(data){
          //alert()
          //console.log(data.data)
          _this.$notify(data.data.msg)
          //alert(data.data.msg)
        })
      },
      del(){
        var _this=this
        axios({
          method:'get',
          url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/userAddress/del.do',
          params:{
            id:_this.id
          }
        }).then((data)=>{
          /*console.log(data.data.data)
          _this.list=data.data.data
          console.log(_this.list[0].name)
          _this.name=_this.list[0].name
          _this.address=_this.list[0].address
          _this.phone=_this.list[0].tel*/
          alert('删除成功')
        })




      }


    }
  }
</script>

<style scoped>
  .container{
    margin-top: 60px;
  }
  .box{
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .bottom button{
    position: absolute;
    left: 40%;
    bottom: 40px;
    background: #1c8de0;
    color: #fff;

  }
</style>



