<template>
  <div class="hello">
    <van-nav-bar
      :title="name"
      fixed
      left-text="美菜商城"
      right-text="..."

      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
  </div>
</template>

<script>
  export default {
    name: 'Begin',
    props:['name'],
    data () {
      return {
        title:'主页'
      }
    },
    methods:{
      onClickLeft(){
        this.$router.go()
      },
      onClickRight(){
        this.$dialog.alert({
          message: '提示'
        });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



