<template>
  <div>
    <Begin :name="title"/>
    <div class="container">
      <router-view @totitle="tag"></router-view>
    </div>

  </div>

</template>

<script>
  import Begin from '@/components/Begin'
    export default {
        name: "Main",
      data(){
          return{
            title:''
          }
      },
      components:{
          Begin
      },
      methods:{
          tag(msg){
       this.title=msg
          }
      }
    }
</script>

<style scoped>
.container{
  margin-top:41px ;
}
</style>



