<template>
    <div>
      <Set/>
      <div class="container">
<!--        <button @click="login">请登陆</button>-->
      </div>
      <van-tabbar v-model="active" fixed>

        <van-tabbar-item icon="home-o" to="/page">首页</van-tabbar-item>
        <van-tabbar-item icon="search" to="/search">全部菜品</van-tabbar-item>
        <van-tabbar-item icon="friends-o"  to="/friends">常用清单</van-tabbar-item>
        <van-tabbar-item icon="friends-o"to="/cart">购物车</van-tabbar-item>
        <van-tabbar-item icon="setting-o"  to="/setting">设置</van-tabbar-item>
      </van-tabbar>

      <div class="head">
        <span><img :src="src"></span>



        <strong><van-uploader :after-read="onRead" class="pho">
          <van-icon name="photograph"/>
        </van-uploader></strong>
        <em>用户名:{{this.$store.state.usernames}}</em>
      </div>

      <div class="money">
        <dl>
          <span>0.00</span>
          <span>余额</span>
        </dl>
        <dl>
          <span>0.00</span>
          <span>欠款</span>
        </dl>
        <dl @click="cheap">
          <span>3</span>
          <span>优惠券</span>
        </dl>

      </div>
      <div class="dd">
        <span>我的订单</span>
        <strong><van-icon name="arrow"/></strong>
      </div>
      <div class="buy">
        <div>
          <span></span>
          <span>代收款</span>
        </div>
        <div>
          <span></span>
          <span>代发货</span>
        </div>
        <div>
          <span></span>
          <span>代收货</span>
        </div>
        <div>
          <span></span>
          <span>售后服务</span>
        </div>
        <div>
          <span></span>
          <span>问题反馈</span>
        </div>
      </div>

      <div class="cz">
        <span>我的充值</span>
        <strong><van-icon name="arrow"/></strong>
      </div>

      <div class="cz1">
        <span>￥500</span>
      </div>

      <div class="cz3">
        <span>立即充值</span>
<!--        {{this.$store.state.userid}}-->
      </div>

    </div>
</template>

<script>
  import axios from 'axios';
  import {mapState} from 'vuex';

  import Set from '@/components/Set'
    export default {
        name: "Setting",
      data(){
        return{
          title:'设置',
          active:4,
          src:'https://p.ssl.qhimg.com/dmtfd/178_100_/t01361ac1ab4d9b2637.webp?size=622x900'

        }
      },
      computed:{
        ...mapState(['count'])


      },
      components:{
          Set
      },
      mounted(){
        this.$emit('toTitle',this.title)
      },
      methods:{
          login(){
            this.$router.push('/login')


          },
        cheap(){
          this.$router.push('/voucher')
        },

        onRead(file){
            //console.log(file.content)
          console.log(this.$store.state.userid)
          var _this=this;
          axios({
            method:'post',
            url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/uploadImg/uploadUserHeadImg.do',
            data:{
              file:file.content,
              userId:_this.$store.state.userid
            }
          }).then(function(data){
            console.log(data.data.data)
          })



          axios({
            method:'get',
            url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/user/getHeadImgByUserId.do',
            data:{
              userId:_this.$store.state.userid
            }
          }).then(function(data){
            console.log(data)
            _this.src=data.data
          })
        },

      }
    }
</script>

<style scoped>
  .container{
    margin-top: 46px;
    margin-bottom: 50px;
  }
  .head{
    width: 100%;
    height: 100px;
    border:1px solid #ccc ;
  }
  .head span img{
    display: block;
    width: 80px;
    height: 80px;
    border: 1px solid #ccc;
    border-radius: 50%;
    float: left;
    margin-top: 10px;
    margin-left: 5px;
    
  }

  .head em{
    display: block;
    float: left;
    line-height: 100px;
    margin-left: 5px;


  }
  .head strong{
    float: left;
   display: block;
    margin-top: 33px;

    margin-left: 5px;
    font-size: 33px;

  }
  .money{
    margin: 0 auto;
    width: 80%;
    height: 80px;
    border: 1px solid #ccc;
    display: flex;
    justify-content: space-around;
    color: red;
    font-size: 14px;

  }
  .money span{
    text-align: center;
  }
  .money dl{

    display: flex;
    flex-direction: column;
    justify-content: space-between;

  }
  .dd{
    width: 100%;
    height: 30px;
    margin-left: 5px;
  }
.buy{
  height: 100px;
  display: flex;
  justify-content: space-around;
}
  .buy div span:first-child{
    display: block;
    width: 50px;
    height: 50px;
    border: 1px solid #ccc;
  }
  .cz{
    width: 100%;
    height: 30px;
    margin-left: 5px;
  }
  .cz1{
    width:40%;
    margin-left: 5px;
    height: 70px;
    border: 1px solid #ccc;
    display: flex;
    justify-content: center;
    align-items: center;

  }
  .cz3{
    margin: 0 auto;

    width:40%;
    height: 30px;
    border: 1px solid #ccc;
    display: flex;
    justify-content: center;
    align-items: center;

  }
</style>



