<template>
  <div class="hello">

    <van-nav-bar
      :title="name"
      fixed
      left-text=""
      right-text=""
      @click-left="onClickLeft"
    >
      <van-icon @click="tag" name="setting" slot="right" size="25px" color="#ccc" style="margin-right: 10px" />

      <van-icon @click="info" name="chat" slot="right" size="25px" color="#ccc" />
    </van-nav-bar>

  </div>
</template>

<script>

  export default {
    name: 'Set',
    props:['name'],
    data () {
      return {
        msg: 'public head',
        title:'主页'
      }
    },
    methods:{
      onClickLeft(){
        this.$router.go(-1)
      },
      onClickRight(){
        this.$router.push('/chat')

      },
      tag(){
        this.$router.push('/backlogin')
      },
      info(){
        this.$router.push('/new')
      }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



