<template>
  <div class="hello">
    <van-nav-bar
      :title="name"
      fixed

      left-text="返回"
      right-text="..."
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />

  </div>
</template>

<script>

  export default {
    name: 'Top',
    props:['name'],
    data () {

      return {

        title:'主页'
      }
    },
    methods:{
      onClickLeft(){
        this.$router.go(-1)
      },
      onClickRight(){
        this.$dialog.alert({
          message: '提示'
        });

      }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



