<template>
  <div>
    <div class="container"></div>
    <van-cell-group>
      <van-field
        v-model="nick"
        required
        clearable
        label="nick"

        placeholder="昵称"

      />
      <van-field
        v-model="password"
        required
        clearable
        label="password"
        placeholder="password"

      />

      <van-field
        v-model="phone"
        type="phone"
        label="phone"
        placeholder="phone"
        required
      />
    </van-cell-group>
    <div class="login">
    <button class="btn" @click="tag">注册提交</button>
      </div>
    <br>
<!--    <h1>{{this.$store.state.count}}</h1>-->
    <router-link to="/login">已有账号？去登录</router-link>
  </div>
</template>

<script>
  import axios from 'axios';
  export default {
    name: "Register",
    data(){
      return{

        password:'',
        phone:'',
        nick:'',
        title:'注册'
      }

    },
    mounted() {
      this.$emit('totitle',this.title)
    },
    methods:{

      tag(){
        var _this=this
        // function num(str) {
        //   var reg=/^[0-9]{6,12}$/;   /*定义验证表达式*/
        //   return reg.test(str);     /*进行验证*/
        // }

        function phone(str) {
          var reg= /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/;
          return reg.test(str);
        }
        var aaa=phone(this.phone)

        if(aaa==false){
          _this.$notify('电话格式不正确')

        }else {

          axios({
            method:'post',
            url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/user/register.do',
            data:{
              nick:this.nick,
              password:this.password,
              phone:this.phone
            }
          }).then(function(data){
            var str=data.data
            _this.$notify(str.msg);
            console.log(str)

            if(str.code==1){

              _this.$router.push('/login')
            }
          })



        }


      },
    }
  }
</script>

<style scoped>

  .container{
    margin-top: 50px;
  }
  .btn{
    width: 200px;
    height: 40px;
    border-radius: 9px;
    background: #1c8de0;
    color: #fff;
  }
  .login{
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>



