<template>
    <div class="container">
      <Top :name="title"/>
      <div class="safe">
        <span style="margin-left: 5px">账户与安全</span>
        <div style="margin-right: 10px">
        <span style="margin-right: 40px;color: #ccc">可修改密码</span>
        <strong><van-icon name="arrow"/></strong>
        </div>
      </div>

      <div class="safe">
        <span style="margin-left: 5px">第三方账户管理</span>
        <strong style="margin-right: 10px"><van-icon name="arrow"/></strong>

      </div>
      <div class="safe">
        <span style="margin-left: 5px">清除缓存</span>
        <strong style="margin-right: 10px"><van-icon name="arrow"/></strong>

      </div>
      <div class="safe">
        <span style="margin-left: 5px">检查更新</span>
        <div style="margin-right: 10px">
          <span style="margin-right: 40px;color: #ccc">当前版本(v2.6.3)</span>
          <strong><van-icon name="arrow"/></strong>
        </div>
      </div>

      <div class="back">
        <div class="bk" @click="back">
          退出账号
        </div>
      </div>
    </div>
</template>

<script>
  import Top from '@/components/Top'
    export default {
        name: "Backlogin",
        data(){
          return{
            title:'设置'
          }

        },

      components:{
          Top
      },
      methods:{

          back(){
            //this.token()
            this.$router.push('/login')
          }
      }
    }
</script>

<style scoped>
  .container{
    margin-top: 46px;
    margin-bottom: 50px;
  }
  .safe{

    width: 100%;
    height: 60px;
    border: 1px solid #ccc;
    display: flex;
    justify-content: space-between;
    align-items: center;
  ;
  }
  .back{
    width: 100%;
    height: 100px;
    border: 1px solid #ccc;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .back .bk{
    border: 1px solid #ccc;
    width: 220px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    background: #00B7FF;
    color: #fff;
  }
</style>



