<template>
    <div>
      <van-tabs>
        <van-tab v-for="(item,k) in list" :title="item.type">
           <van-card
    v-for="it in item.foodList"
    :price="it.foodPrice"
    :desc="'库存:'+it.foodRepertory"
    :title="it.foodName"
    :thumb="it.foodImg"
    @click="detail(it.id)"
  />

        </van-tab>
      </van-tabs>




    </div>

 <!-- <van-card
    v-for="item in list"
    :price="11"
    :desc="item.desc"
    :title="item.title"
    :thumb="item.img"
  />-->
</template>

<script>
  import axios from 'axios'

    export default {
        name: "Search",
      data(){
        return{
          title:'搜索',
          active: 0,
          list:[]
        }
      },
      mounted(){

          var _this=this
        this.$emit('toTitle',this.title)


        axios({
          method:'get',
          url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/food/all.do',
        }).then((data)=>{
          console.log(data.data.data)
          _this.list=data.data.data

        })
      },
      methods:{

        detail(id){
          this.$router.push({name:'Detail',query:{id:id}})

        }



      }
    }
</script>

<style scoped>

</style>



