<template>
  <div>
    <Free/>
    <div class="container">
    </div>
      <van-card

        tag="热卖"
        :price="item.pprice"
        :desc="item.desc"
        :title="item.pname"
        :thumb="item.pimg"
        :origin-price="item.pprice*2"
      >
        <div slot="footer">

          <van-button size="mini" @click="jian">-</van-button>

          <input type="text" style="width: 25px;border: 1px solid #ccc;text-align: center" placeholder="1" v-model="input" v-if="input=input>0?input:1">
          <van-button size="mini" @click="plus">+</van-button>

<!--          <van-button size="mini" @click="-->
<!--add">加入购物车</van-button>-->

        </div>
      </van-card>

    <van-goods-action>
      <van-goods-action-mini-btn
        icon="chat-o"
        text="客服"
        @click="server"
      />
      <van-goods-action-mini-btn
        icon="cart-o"
        text="购物车"
        @click="cart"
      />
      <van-goods-action-big-btn
        text="加入购物车"
        @click="add"
      />
      <van-goods-action-big-btn
        primary
        text="立即购买"
        @click="buy"
      />
    </van-goods-action>


  </div>
</template>

<script>
  import {mapState} from 'vuex';

  import Free from '@/components/Free'
  import axios from 'axios';
  export default{
    name:'Detail',
    data(){
      return{
        title:'详情',
        item:'',
        cartData:{},
        list:[],
        input:'',
        num1:''
      }
    },
    computed:{
      ...mapState(['count']),
    },
    mounted(){

      var _this=this;

 /*     axios({
        method:'get',
        url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/food/all.do',
      }).then((data)=>{
        console.log(data.data.data)
        _this.list=data.data.data

        _thislist.map(function (ite,index) {
          ite.map(function (it,i) {
            if(it.id==_this.$route.query.id){
              _this.item=it
            console.log(_this.item)
            }
          })
        })
      })*/

      axios({
        url:'http://jx.xuzhixiang.top/ap/api/detail.php',
        params:{id:_this.$route.query.id}
      }).then((data)=>{
        console.log(data.data.data)
        _this.item=data.data.data
      })
    },
    components:{
      Free
    },
    methods:{
      // add(){
      //   this.$store.commit('add')
      // },
      // jian(){
      //   this.$store.commit('jian')
      // }

      plus(){
        this.input++
      },
      jian(){
        this.input--
      },
      buy(){
        axios({
          method:'get',
          url:'http://47.102.214.131:8081/meicaidining-1.0/limbo/userOrder/all.do',
          params:{limit:1,page:1,userId:1}
        }).then((data)=>{
          console.log(data.data.data)

        })



      },

      server(){
        this.$router.push('/server')
      },
      cart(){
        this.$router.push('/cart')


      },
      add(){
        var id=this.$route.query.id;
        //console.log(id);

        var _this=this;
        axios({
          url:'http://jx.xuzhixiang.top/ap/api/cart-list.php',
          params:{id:1932}
        }).then((data)=>{
          //console.log(data.data.data);
          _this.list=data.data.data

        });
        var list1=this.list.filter((item)=>{
          return item.pid==id;
        })
        console.log(list1)
        //_this.$toast('商品:'+list1[0].pname+''+'数量:'+list1[0].pnum);

        if(list1.length>0){
          //alert('购物车已有此商品')
          var num1=parseInt(this.input)
          axios({
            method:'get',
            url:'http://jx.xuzhixiang.top/ap/api/cart-update-num.php',
            params:{uid:1932,pid:id,pnum:num1}
          }).then((data)=>{
            //console.log(data.data.data)
            //console.log(id)
            //_this.item=data.data.data
            //_this.$notify(str.msg);
            _this.$router.push('/cart')
          })

        }else {
          //alert('购物车无此商品')

          axios({
            method:'get',
            url:'http://jx.xuzhixiang.top/ap/api/add-product.php',
            params:{uid:1932,pid:id,pnum:_this.input}
          }).then((data)=>{
            //console.log(data.data.data)
            _this.$router.push('/cart')

          })



        }




      }

    }
  }
</script>

<style scoped="">
  .container{
    margin-top: 46px;
    margin-bottom: 50px;
  }
</style>



