<template>
  <div class="hello">

    <van-nav-bar
      :title="name"
      fixed

      left-text="返回"
      right-text="..."
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    >
      <van-icon name="balance-pay" slot="right" size="25px" color="#ccc" />
    </van-nav-bar>
  </div>
</template>

<script>

  export default {
    name: 'Top',
    props:['name'],
    data () {
      return {

        title:'主页'
      }
    },
    methods:{
      onClickLeft(){
        this.$router.go(-1)
      },
      onClickRight(){
        this.$router.push('/voucher')

      }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



